<template>
<v-app dark>
  <v-app-bar dark > 
    <country-flag :country="item.countryCode" size="big" rounded ></country-flag>
    {{ item.country }}
    <v-spacer></v-spacer>
    <v-icon>mdi-calendar</v-icon>
    {{ item.date | date }}
  </v-app-bar>
  <v-card dark height="100%" flat class="text-center">
    <v-card-text  style="height: 100%" >
      <h1 style="font-size: 50px;" class="mb-6">ðŸŽ‰New DepositðŸŽ‰</h1>
      <v-card max-width="600" class="mx-auto text-center my-12" flat>
        <v-card-text class="text-center">
          <profile-image :value="item.profileImage" disabled size="200" class="mx-auto"></profile-image>
        </v-card-text>
        <v-card-title >
          <h1 class="mx-auto">{{ item.agent }}</h1>
        </v-card-title>
      </v-card>
      <v-card flat >
        <v-card-text >

          <h1 class="green--text font-weight-bold py-12" style="font-size: 10vw;">{{ item.amount | money }}</h1>
        </v-card-text>
      </v-card>
    </v-card-text>
  </v-card>
</v-app>
</template>
<script>
import utility from './../utility'
import ProfileImage from './../components/ProfileImage'
import CountryFlag from 'vue-country-flag'
import dateforamt from 'dateformat' 
export default {
  name: 'ScreenView',
  components: {
    ProfileImage,
    CountryFlag
  },
  filters: {
    date (value) {
      return dateforamt(value, 'yyyy/mm/dd')
    },
    money (value) {
        return utility.money(value)
    }
  },
  computed: {
    item () {
      return JSON.parse(atob(this.$route.params.data)) || {}
    }
  }
  
}
</script>
